"use client";
import dayjs from "dayjs";

export function Topbar() {
  return (
    <header className="h-14 px-6 bg-brand-light text-white flex items-center justify-between">
      <span className="font-semibold">FSW HMI v1</span>
      <span>{dayjs().format("HH:mm:ss DD/MM/YYYY")}</span>
      <button className="rounded bg-brand-accent px-3 py-1 text-sm">Exit HMI</button>
    </header>
  );
}
